.backDrop {
  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: black;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s;
}

.showBackDrop {
  visibility: visible;
  opacity: 0.8;
}

.overlay {
  z-index: 1500;
  position: absolute;
  padding: 20px;
  width: 30%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* margin-top: -250px;
  margin-left: -15%; */
  background-color: #ffffff;
  border-radius: 20px;
  visibility: hidden;
  transition: visibility 0.1s;
}

.showOverlay {
  visibility: visible;
}
